/* vuex -- actions */
import { getUser } from '@/ajaxApi/getData'

export default {
	// 
	async getUserInfo ({ commit, state }) {
		if (state.login) {	// 
			return
		}
		let res = await getUser()
		res = res.data
		console.log(res)
		// vuex
		if (res.user_id) {	// 
			state.login = true
			commit('GET_USERINFO', res)
		}
	}	
}


