import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

// state
const state = {
	geohash: '',	//  + 
	latitude: '',	// 
	longitude: '',	// 
	cartList: {},	// 
	shopId: null,	// id
	userInfo: null,	// 
	login: false,	// 
}

export default new Vuex.Store({
	state,
	mutations,
	actions
})


